.img_girl{
    transition: all 1s;
    width: 200px;
    height: 300px;
    overflow: hidden;
    box-shadow: 10px 10px 11px #888ADF;
    border-radius: 50px;
}
.img_girl:hover{
    box-shadow: 10px 10px 5px #888C42;
    border-radius: 8px;
}
.nv{
    transition: all 1s;
    width: 200px;
    height: 300px;
    object-fit: cover;
}
.nv:hover {
    transform: scale(1.2);
}
  
